export default {
	data() {
		return {
			detail:'',
			userDetail:''
		};
	},
	computed:{
	   i18n() {  
	     return this.$i18nMsg()  
	   }
	},
	onLoad:function(){
		uni.setNavigationBarTitle({
		            title:this.$i18nMsg().page.power
		        });
		this.fetchUserDetail()
		this.fetchData()
	},
	methods:{
		async fetchUserDetail(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
		},
		async fetchData(){
			const res = await this.$http.get('/dm/bonus')
			this.detail = res.data.data
			console.log(res)
		},
		toDetails:function(e){
			if (e==1){
				uni.navigateTo({
					url: `./dongliA?usde=${this.userDetail.usde}`,
				});
			}else if (e ==2){
				uni.navigateTo({
					url: `./dongliB?usde=${this.userDetail.usde}`,
				});
			}else if (e ==3){
				uni.navigateTo({
					url: `./dongliC?usde=${this.userDetail.usde}`,
				});
			}
		}
	}
	
}
